import { routes } from "../router/index";
import axios from "axios";

axios.defaults.baseURL = "http://10.164.9.118:9010/api/";
// if (document.location.hostname === "localhost") {
//   /* Production enviroment */
//   axios.defaults.baseURL = "http://localhost:9010/api/";
//   console.log("Development_enviroment !");
// }
axios.defaults.crossDomain = true;

export default {
  // iSampling Python Service Actions

  // Authantication Actions

  initAuth({ commit, dispatch }) {
    let token = localStorage.getItem("token");
    if (token) {
      let expirationDate = localStorage.getItem("expirationDate");
      let time = new Date().getTime();

      commit("setToken", token);
      routes.push("/");
      if (time >= +expirationDate) {
        console.log("token sÃ¼resi geÃ§miÅ...");
        dispatch("logout");
      } else {
        commit("setToken", token);
        let timerSecond = +expirationDate - time;
        // console.log(timerSecond); // Oturum sonlanmasÄ±na kaÃ§ milisaniye kaldÄ±ÄÄ±nÄ± gÃ¶steriyor.
        dispatch("setTimeoutTimer", timerSecond);
        // routes.push("/");
      }
    } else {
      // console.log(token);
      routes.push({ path: "/login" });
      return false;
    }
  },
  login({ commit, dispatch }, auth) {
    const headers = {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json",
    };
    const timeout = 3600000; // 3600000 yani bir saat
    return axios
      .post("login", auth, { headers })
      .then((response) => {
        // console.log(response.data.auth);
        if (response.data.auth) {
          commit("setToken", response.data.token);
          commit("setloginemail", auth.user);
          localStorage.setItem("token", response.data.token);
          localStorage.setItem("username", auth.user); //.replace("@ipsos.com",""));
          localStorage.setItem(
            "expirationDate",
            new Date().getTime() + timeout
          );
          dispatch("setTimeoutTimer", timeout);
        }
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  logout({ commit }) {
    commit("clearToken");
    localStorage.removeItem("token");
    localStorage.removeItem("expirationDate");
    // console.log("logout");
    routes.push({ path: "/login" });
    location.reload();
  },
  setTimeoutTimer({ dispatch }, expiresIn) {
    setTimeout(() => {
      dispatch("logout");
    }, expiresIn);
  },
  getUserLogData({ commit }) {
    axios
      .get("getUserLog")
      .then((response) => {
        // console.log(response.data);
        commit("setUserLogData", response.data);
      })
      .catch((error) => {
        console.log(error);
        // this.errored = true;
      });
    //  .finally(() => (this.loading = false));
  },
  Calculate({ commit, state }) {
    const headers = {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "application/json;charset=UTF-8",
    };
    const payload = {
      data: JSON.stringify(state.isamplingdata),
      loginuser: JSON.stringify(state.loginemail),
    };
    // console.log(payload);
    return axios({
      url: "download",
      headers: headers,
      method: "POST",
      params: payload,
      responseType: "blob", // important
    })
      .then((response) => {
        // console.log(response.status);
        if (response.data && response.status == 200) {
          const filename = "isampling.xlsx"; // bu isim sonrasÄ±nda deÄiÅecek.
          const blobObject = new Blob([response.data]);
          const url = window.URL.createObjectURL(blobObject);
          if (window.navigator && window.navigator.msSaveOrOpenBlob) {
            window.navigator.msSaveBlob(blobObject, filename);
          } else {
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute("download", filename); //or any other extension
            document.body.appendChild(link);
            link.click();
          }
          commit("setDownloadstatus", true);
        }
      })
      .catch(function(error) {
        console.log(error);
        commit("setDownloadstatus", false);
      });
  },
};
