        >
          <v-list-item-icon>
            <v-icon color="turkuaz" style="font-size: 25px;">{{ link.icon }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ link.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item
          to="/about"
          v-ripple="{ class: 'primary--text' }"
          :disabled="getisamplingloading"
          color="primary"
          link
        >
          <v-list-item-icon>
            <v-icon color="turkuaz" style="font-size: 25px;">mdi-information</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>HakkÄ±nda</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <template v-slot:append>
        <div class="text-center pa-6">
          <v-btn
            v-show="isAuthenticated"
            small
            block
