</template>

<script>
import { mapGetters } from "vuex";

export default {
  data: () => ({
    drawer: true,
    showusername: "",
    useravatar: "",
  }),
  computed: {
    ...mapGetters([
      "getloginemail",
      "getavatarname",
      "isAuthenticated",
      "getisamplingloading",
    ]),
    links() {
      return [
        {
          icon: "mdi-chart-box-plus-outline",
          text: "iSampling",
          route: "/",
          disabled: this.getisamplingloading,
        },
        {
          icon: "mdi-text-account",
          text: "KullanÄ±cÄ± KayÄ±tlarÄ±",
          route: "/userlog",
          disabled: this.getisamplingloading,
        },
        // { icon: "mdi-database", text: "Admin", route: "/admin", disabled: true },
      ];
    },
  },
  methods: {
    LoggingOut() {
      this.$store.dispatch("logout");
      this.$router.push("/login");
      this.drawer = false;
    },
  },
};
</script>
