<template>
  <v-col cols="12" sm="6" id="kirkent" class="pa-2 my-0">
    <v-select
      :items="formdata.kirkent.items"
      v-model="formdata.kirkent.selection"
      label="KÄ±r-Kent"
      item-color="itemcolor"
      :return-object="true"
      @change="M_Selectioner(formdata.kirkent.selection, 'Upd_kirkent')"
      :rules="[kirkentrequired]"
      :error="kirkent_validation"
      :prepend-inner-icon="kirkent_icon"
      :validate-on-blur="true"
      multiple
      outlined
      dense
    >
      <template #selection="{ item }">
        <v-chip
          color="chipcolor"
          text-color="white"
          small
        >
          {{ item.text }}
        </v-chip>
      </template>
    </v-select>
  </v-col>
</template>

<script>
import { mapMutations } from "vuex";
import { Kirkent } from "../../mixins/dataKirkent";
import { sharedFunc } from "../../mixins/sharedFunc";

export default {
  mixins: [Kirkent, sharedFunc],
  computed: {
    kirkent_validation() {
      const val = this.formdata.kirkent.selection;
      if ((val.length == 1 && val[0].value == 1) || val.length == 0) {
        return true;
      }
      return false;
    },
    kirkent_icon(){
      const val = this.formdata.kirkent.selection;
      if (val.length == 1 && val[0].value == 2)  {
        return "mdi-city";
      } 
      else if (val.length == 0 )  {
        return "mdi-alert";
      }       
      return "mdi-tractor-variant"     
    },
  },
  methods: {
    ...mapMutations(["Upd_kirkent"]),
    kirkentrequired(value) {
      if (value instanceof Array && value.length == 1 && value[0].value == 1) {
        return "KÄ±r tanÄ±mÄ± TÃ¼rkiye'nin sadece 8% ini kapsamaktadÄ±r!";
      }
      if (value instanceof Array && value.length == 0) {
        return "En az birini seÃ§in!";
      }
      return !!value || "Required.";
    },
  },
};
</script>

<style scoped>
@media (max-width: 1500px) {
  #kirkent {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}
</style>
