<template>
  <v-col cols="12" sm="6" id="cinsiyet" class="pa-2">
    <v-select
      :items="formdata.cinsiyet.items"
      v-model="formdata.cinsiyet.selection"
      item-color="itemcolor"
      label="Cinsiyet"
      @change="M_Selectioner(formdata.cinsiyet.selection, 'Upd_cinsiyet')"
      :return-object="true"
      :error="formdata.cinsiyet.selection.length == 0 ? true : false"
      :prepend-inner-icon="malefemale_icon"
      multiple
      outlined
      dense
    >
      <template #selection="{ item }">
        <v-chip
          color="chipcolor"
          text-color="white"
          small
        >
         {{ item.text }}
        </v-chip>
      </template>
    </v-select>
  </v-col>
</template>

<script>
import { mapMutations } from "vuex";
import { Cinsiyet } from "../../mixins/dataCinsiyet";
import { sharedFunc } from "../../mixins/sharedFunc";

export default {
  mixins: [Cinsiyet,sharedFunc], 
  methods: {
    ...mapMutations(["Upd_cinsiyet",]),
  },
  computed: {
    malefemale_icon() {
      const val = this.formdata.cinsiyet.selection;
      if ( val.length == 1 && val.map((x) => x.value)[0] == 1  ) {
        return  "mdi-human-female";
      }
      else if ( val.length == 1 && val.map((x) => x.value)[0] == 2  ) {
        return "mdi-human-male";
      }      
      else if ( val.length == 0 ) {
        return "mdi-alert";
      }            
      else {
        return "mdi-human-male-female";
      }
    }
  },
};
</script>

<style scoped>
@media (max-width: 1500px) {
  #cinsiyet {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}
</style>
