<template>
  <div>
    <v-row id="cografya" class="pa-2">
      <v-select
        :items="formdata.cografya.items"
        v-model="formdata.cografya.selection"
        @change="Mcografyachange"
        :return-object="true"
        label="CoÄrafi Kapsam"
        prepend-inner-icon="mdi-chart-bubble"        
        dense
        outlined
      ></v-select>
    </v-row>
    <v-row id="cografya" class="pa-2">
      <v-col :cols="cografyacols" class="pa-0 my-0">
        <v-combobox
          :items="formdata.cografya.defaultvalues"
          v-model="formdata.cografya.defaultselection"
          placeholder="CoÄrafi Kapsam seÃ§iniz."
          item-color="itemcolor"
          :auto-select-first="true"
          @change="
            M_Selectioner(
              formdata.cografya.defaultselection,
              'Upd_cografyaselection'
            );
            M_Selectioner(new Array(), 'Upd_ekcografyaselection');
          "
          :return-object="true"
          required
          :error="cografya_validation"
          :prepend-inner-icon="formdata.cografya.defaultselection.length == 0 ? 'mdi-alert' : 'mdi-near-me'"
          counter
          outlined
          multiple
          clearable
          dense
        >
          <template v-slot:selection="{ item, index }">
            <v-chip
              v-if="index < 2"
              color="chipcolor"
              text-color="white"
              :small="true"
            >
              <span>{{ item.text }}</span>
            </v-chip>
            <span v-if="index === 2" class="grey--text caption"
              >&nbsp; (+{{ formdata.cografya.defaultselection.length - 2 }}
              diÄer)
            </span>
          </template>
        </v-combobox>
      </v-col>
      <v-col :cols="1" v-show="cografyacols == 11" class="pa-0 ml-0">
        <v-btn
          icon
          :disabled="
            formdata.cografya.defaultselection.length > 0 ? false : true
          "
          @click="cografyadialog(true)"
        >
          <v-icon
            text
            :color="cografya_validation == true ? 'error' : '#2d6187'"
            elevation="3"
            x-large
            >mdi-plus-thick</v-icon
          >
        </v-btn>
      </v-col>
    </v-row>
    <CografyaDialog />
  </div>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
import { Cografya } from "../../mixins/dataCografya";
import { sharedFunc } from "../../mixins/sharedFunc";
import CografyaDialog from "../CografyaDialog";

export default {
  mixins: [Cografya, sharedFunc],
  data() {
    return {
      cografyacols: 12,
    };
  },
  components: {
    CografyaDialog,
  },
  computed: {
    ...mapGetters(["getcografyaselection", "getekcografyaselection"]),
    cografya_validation() {
      const initial =
        this.formdata.cografya.selection.value == undefined
          ? 1
          : this.formdata.cografya.selection.value;
      let ekcografya = this.getekcografyaselection.length;
      if (this.formdata.cografya.defaultselection.length == 0 && initial < 4) {
        return true;
      }
      if (initial > 3 && ekcografya == 0) {
        return true;
      }
      return false;
    },
  },
  methods: {
    ...mapMutations([
      "Upd_cografya",
      "Upd_cografyaselection",
      "Upd_ekcografyaselection",
      "setcografyadialog",
    ]),
    Mcografyachange(e) {
      const cografyamap = {
        1: "nuts1",
        2: "nuts2",
        3: "il",
        4: "nuts1",
        5: "nuts2",
      };
      const val = this.formdata.cografya[cografyamap[e.value]];
      this.formdata.cografya.defaultselection = val.selection;
      this.formdata.cografya.defaultvalues = val.items;
      e.value == 4 || e.value == 5
        ? (this.cografyacols = 11)
        : (this.cografyacols = 12);
      this.Upd_cografya(this.formdata.cografya.selection.value);
      this.Upd_cografyaselection(val.selection.map((val) => val.value));
      this.Upd_ekcografyaselection(new Array());
    },
    cografyadialog(e) {
      if (this.getcografyaselection.length > 0) {
        this.setcografyadialog(e);
      }
    },
  },
};
</script>

<style scoped>
@media (max-width: 1500px) {
  #cografya {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}
</style>
