<template>
  <div class="app">
    <v-app>
      <v-dialog
        mode="out-in"
        v-model="dialog"
        persistent
        max-width="600px"
        min-width="360px"
      >
        <div>
          <v-tabs
            v-model="tab"
            show-arrows
            background-color="turkuaz"
            icons-and-text
            dark
            grow
            :loading="submitloading"
          >
            <v-tabs-slider color="purple darken-4"></v-tabs-slider>
            <v-tab v-for="i in tabs" :key="i.id">
              <v-icon large>{{ i.icon }}</v-icon>
              <div class="caption py-1">{{ i.name }}</div>
            </v-tab>
            <v-tab-item>
              <v-card class="px-4">
                <v-card-text>
                  <v-form ref="loginForm" v-model="valid" lazy-validation>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field
                          v-model="loginEmail"
                          :rules="loginEmailRules"
                          label="E-mail"
                          required
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field
                          v-model="loginPassword"
                          :append-icon="show1 ? 'eye' : 'eye-off'"
                          :rules="[rules.required, rules.min]"
                          :type="show1 ? 'text' : 'password'"
                          name="input-10-1"
                          label="Åifre"
                          hint="LÃ¼tfen parolanÄ±zÄ± giriniz."
                          counter
                          @click:append="show1 = !show1"
                        ></v-text-field>
                      </v-col>
                      <v-col class="d-flex" cols="12" sm="6" xsm="12"> </v-col>
                      <v-spacer></v-spacer>
                      <v-col class="d-flex" cols="12" sm="3" xsm="12" align-end>
                        <v-btn
                          class="white--text"
                          md
                          block
                          :loading="submitloading"
                          :disabled="!valid"
                          color="itemcolor"
                          @click="validate"
                          @keyup.enter="validate"
                        >
                          Login
                        </v-btn>
                      </v-col>
                    </v-row>
                  </v-form>
                </v-card-text>
              </v-card>
            </v-tab-item>
          </v-tabs>
        </div>
      </v-dialog>
      <v-snackbar v-model="snackbar" rounded="pill">
        {{ snacktext }}
        <template v-slot:action="{ attrs }">
          <v-btn color="lime" text v-bind="attrs" @click="snackbar = false">
            Close
          </v-btn>
        </template>
      </v-snackbar>
    </v-app>
  </div>
</template>

<script>
export default {
  computed: {
    passwordMatch() {
      return () => this.password === this.verify || "Password must match";
    },
  },
  methods: {
    validate() {
      if (this.$refs.loginForm.validate()) {
        // console.log("clicked submit");
        this.submitloading = true;
        const user = { user: this.loginEmail, passw: this.loginPassword };
        this.$store.dispatch("login", user).then(() => {
          if (this.$store.getters.isAuthenticated) {
            this.submitloading = false;
            this.$router.push("/");
          } else {
            this.loginPassword = "";
            this.snackbar = true;
            this.submitloading = false;
          }
        });
        // console.log(this.$store.getters.isAuthenticated);
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
  },
  data: () => ({
    dialog: true,
    submitloading: false,
    snackbar: false,
    snacktext: "KullanÄ±cÄ± adÄ± ya da Åifreniz hatalÄ±!",
    tab: 0,
    tabs: [{ id: 1, name: "Login", icon: "mdi-account" }],
    valid: true,
    firstName: "",
    lastName: "",
    email: "",
    password: "",
    verify: "",
    loginPassword: "",
    loginEmail: localStorage.getItem("username") || "",
    loginEmailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+\..+/.test(v) || "E-mail must be valid",
    ],
    show1: false,
    rules: {
      required: (value) => !!value || "Password Required.",
      min: (v) => (v && v.length >= 4) || "Too short.",
    },
  }),
};
</script>
