<template>
  <v-row justify="center">
    <v-dialog v-model="yasdialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Ãzel YaÅ SeÃ§imi</span>
        </v-card-title>
        <v-card-text v-show="error.length > 0">
          {{ error }}
        </v-card-text>
        <v-card-text v-show="error.length == 0">
          <v-container>
            <v-row>
              <v-col cols="10">
                <v-select
                  id="app"
                  ref="autocomplete"
                  v-model="values"
                  :items="items"
                  label="YaÅ"
                  multiple
                  :return-object="true"
                  :counter="maxSelected"
                  :menu-props="menuProps"
                  small-chips
                  clearable
                  dense
                  item-color="chipcolor"
                  :persistent-hint="true"
                  hint="SeÃ§mek istediÄiniz yaÅ aralÄ±ÄÄ± iÃ§in alt ve Ã¼st deÄeri seÃ§ip ekleye basmanÄ±z yeterlidir."
                >
                </v-select>
              </v-col>
              <v-col cols="2">
                <v-btn
                  :disabled="EkleStatus"
                  block
                  dark
                  color="chipcolor"
                  :elevation="EkleStatus ? 0 : 2"
                  @click="customYasEkle"
                  small
                  >Ekle</v-btn
                >
              </v-col>
            </v-row>
          </v-container>
          <!-- <small></small> -->
          <div>
            <v-chip
              v-for="c in customyas"
              :key="c"
              class="ma-2"
              color="chipcolor"
              text-color="white"
              close
              @click:close="delChip(c, customyas)"
            >
              {{ c }}
            </v-chip>
          </div>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="red lighten-2" text @click="clearcustomyas">
            Kapat
          </v-btn>
          <v-btn
            color="success"
            text
            :disabled="error.length > 0"
            @click="setvalue"
          >
            Kaydet
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { sharedFunc } from "../mixins/sharedFunc";

export default {
  mixins: [sharedFunc],
  data: () => ({
    values: [],
    items: [...Array(76).keys()],
    customyas: [],
    controlarr: [],
    error: "",
    maxSelected: 2,
    searchString: "",
  }),
  computed: {
    ...mapGetters(["yasdialog"]),
    EkleStatus() {
      return this.values.length >= 2 ? false : true;
    },
    menuProps() {
      const menuProps = {
        disabled: this.values.length >= 2 ? true : false,
      };
      return menuProps;
    },
  },
  methods: {
    ...mapMutations([
      "Upd_ekyasselection",
      "Upd_ozelyasselection",
      "setyasdialog",
    ]),
    M_yasdialog(e) {
      this.setyasdialog(e);
    },
    setvalue() {
      this.M_yasdialog(false);
      const payload = this.customyas;
      this.Upd_ekyasselection(payload);
      const selection = [];
      payload.forEach(function(data, index) {
        selection.push({ text: data, value: index + 1 });
      });
      this.Upd_ozelyasselection(selection);
    },
    customYasEkle() {
      if (this.values.length > 0) {
        const min = Math.min(...this.values),
          max = Math.max(...this.values);
        const val = min + "-" + max;
        const rangearray = this.createrangearray(min, max);
        this.controlarr.push.apply(this.controlarr, rangearray);
        if (this.controlarr.length == [...new Set(this.controlarr)].length) {
          this.customyas.push(val);
          for (var k in rangearray) {
            const index = this.items.indexOf(rangearray[k]);
            if (index > -1) {
              this.items.splice(index, 1);
              this.values = [];
            }
          }
        } else {
          this.error = "Birbiri ile Ã§eliÅen deÄiÅkenler seÃ§tiniz!";
        }
      }
    },
    clearcustomyas() {
      this.M_yasdialog(false);
      this.customyas = [];
      this.items = [...Array(76).keys()];
      this.Upd_ozelyasselection([]);
      this.error = "";
      this.controlarr = [];
      this.values = [];
    },
    createrangearray(start, end) {
      return new Array(end + 1 - start).fill().map((d, i) => i + start);
    },
    delChip(itemNeedToRemove, array) {
      this.deleteChip(itemNeedToRemove, array);
      const min = parseInt(itemNeedToRemove.split("-")[0]);
      const max = parseInt(itemNeedToRemove.split("-")[1]);
      const ary = this.createrangearray(min, max);
      ary.forEach((element) => this.deleteChip(element, this.controlarr));
      ary.forEach((element) => this.items.push(element));
      this.items = this.items.sort(function(a, b) {
        return a - b;
      });
    },
  },
};
</script>

