export default {
  isAuthenticated(state) {
    if (+state.token) {
      return true;
    } else {
      return false;
    }
  },
  cografyadialog(state) {
    return state.cografyadialogshow;
  },
  yasdialog(state) {
    return state.yasdialogshow;
  },
  getloginemail(state) {
    return state.loginemail;
  },
  getavatarname(state) {
    const uname = state.loginemail.replace("@ipsos.com", "").split(".");
    if (uname[0] != "") {
      return uname.map((uname) => uname[0].toUpperCase()).join("");
    }
  },
  getcografyaselection(state) {
    return state.isamplingdata.cografyaselection;
  },
  getekcografyaselection(state) {
    return state.isamplingdata.ekcografyaselection;
  },
  getcografya(state) {
    return state.isamplingdata.cografya;
  },
  getozelyasselection(state) {
    return state.ozelyasselection;
  },
  getUserLogData(state) {
    return state.UserLogData;
  },
  getDownloadstatus(state) {
    return state.downloadstatus;
  },
  getorneklemadet(state) {
    return state.isamplingdata.orneklemadet;
  },
  getisamplingloading(state){
    return state.isamplingloading;
  },
};
