<template>
  <v-form ref="form" v-model="valid" lazy-validation>
    <v-container style="maxWidth: 1250px;">
      <v-card :loading="formloading" class="my-auto">
        <v-container style="maxWidth: 1200px;">
          <v-row>
            <BireyHane />
            <KirKent />
            <Cinsiyet />
            <Egitim />
          </v-row>
        </v-container>
      </v-card>
      <v-row>
        <v-col cols="12" sm="6">
          <v-card :loading="formloading" id="card2">
            <v-container>
              <Cografya />
              <Ses />
            </v-container>
          </v-card>
        </v-col>

        <v-col cols="12" sm="6">
          <v-card :loading="formloading" id="card1">
            <v-container>
              <Yas />
              <OrneklemAdet />
              <v-row class="py-7">
                <v-col id="hatapayÄ±" cols="6">
                  <v-chip elevation="4" label
                    >Hata PayÄ± : % {{ hatapayÄ±hesap }}</v-chip
                  >
                </v-col>
                <v-col cols="0"></v-col>
                <v-col id="calculate" cols="5">
                  <v-btn
                    :disabled="!valid || getisamplingloading"
                    :block="true"
                    elevation="2"
                    color="indigo darken-4"
                    class="pa-1 rounded-lg"
                    outlined
                    @click="Calculate"
                  >
                    Hesapla
                  </v-btn>
                </v-col>
                <v-col cols="1"></v-col>
                <v-snackbar v-model="snackbar" rounded="pill">
                  {{ snackbartext }}
                  <template v-slot:action="{ attrs }">
                    <v-btn
                      color="white"
                      text
                      v-bind="attrs"
                      @click="snackbar = false"
                    >
                      Close
                    </v-btn>
                  </template>
                </v-snackbar>
                <v-overlay :value="getisamplingloading">
                  <v-progress-circular
                    indeterminate
                    size="64"
                  ></v-progress-circular>
                </v-overlay>
              </v-row>
            </v-container>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
// ISampling Child Components
import BireyHane from "../components/isamplingcomponents/BireyHane";
import KirKent from "../components/isamplingcomponents/KirKent";
import Cinsiyet from "../components/isamplingcomponents/Cinsiyet";
import Egitim from "../components/isamplingcomponents/Egitim";
import Cografya from "../components/isamplingcomponents/Cografya";
import Ses from "../components/isamplingcomponents/Ses";
import Yas from "../components/isamplingcomponents/Yas";
import OrneklemAdet from "../components/isamplingcomponents/OrneklemAdet";

export default {
  components: {
    BireyHane,
    KirKent,
    Cinsiyet,
    Egitim,
    Cografya,
    Ses,
    Yas,
    OrneklemAdet,
  },
  data() {
    return {
      formloading: true,
      valid: true,
      snackbar: false,
      snackbartext: "",
      attrs: {
        class: "mb-6",
        boilerplate: true,
        elevation: 2,
      },
    };
  },
  mounted() {
    setTimeout(() => {
      this.formloading = false;
    }, 400);
  },
  computed: {
    ...mapGetters([
      "getDownloadstatus",
      "getorneklemadet",
      "getisamplingloading",
    ]),
    hatapayÄ±hesap() {
      const val = this.getorneklemadet;
      if (val > 0) {
        return (1.96 * Math.pow((0.5 * 0.5) / val, 0.5) * 100).toFixed(1);
      }
      return "";
    },
  },
  methods: {
    ...mapMutations(["setisamplingloading"]),
    Calculate() {
      this.setisamplingloading(true);
      // this.$vuetify.theme.themes.light.primary = "#b0bec5";
      this.snackbartext = "DatanÄ±z oluÅturuldu.";
      this.$store.dispatch("Calculate").then(() => {
        this.setisamplingloading(false);
        if (!this.getDownloadstatus) {
          this.snackbartext = "Data oluÅturma sÄ±rasÄ±nda hata ile karÅÄ±laÅÄ±ldÄ±.";
        }
        this.snackbar = true;
        location.reload(true);
      });
    },
  },
};
</script>

<style>
@media (max-width: 1500px) {
  #calculate,
  #hatapayÄ± {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
  #card1,
  #card2 {
    height: 299px !important;
  }
}
@media (min-width: 1501px) {
  #card1,
  #card2 {
    height: 352px !important;
  }
}

.v-label.theme--light {
  /* color: black; */
  font-weight: bold;
}

.v-icon.v-icon {
  font-size: 19px;
}
</style>
