export default {
  Upd_bireyhane(state, value) {
    state.isamplingdata.bireyhane = value;
  },
  Upd_cinsiyet(state, value) {
    state.isamplingdata.cinsiyet = value;
  },
  Upd_kirkent(state, value) {
    state.isamplingdata.kirkent = value;
  },
  Upd_egitim(state, value) {
    state.isamplingdata.egitim = value;
  },
  Upd_cografya(state, value) {
    state.isamplingdata.cografya = value;
  },
  Upd_ekcografyaselection(state, value) {
    state.isamplingdata.ekcografyaselection = value;
  },
  Upd_cografyaselection(state, value) {
    state.isamplingdata.cografyaselection = value;
  },
  Upd_yas(state, value) {
    state.isamplingdata.yas = value;
  },
  Upd_yasselection(state, value) {
    state.isamplingdata.yasselection = value.sort(function(a, b) {
      return a - b;
    });
  },
  Upd_ekyasselection(state, value) {
    state.isamplingdata.ekyasselection = value.sort(function(a, b) {
      return a.split("-")[0] - b.split("-")[0];
    });
  },
  Upd_ozelyasselection(state, value) {
    state.ozelyasselection = value;
  },
  Upd_ses(state, value) {
    state.isamplingdata.ses = value;
  },
  Upd_sesselection(state, value) {
    state.isamplingdata.sesselection = value.sort(function(a, b) {
      return a - b;
    });
  },
  Upd_orneklemadet(state, value) {
    state.isamplingdata.orneklemadet = value;
  },
  setToken(state, token) {
    state.token = token;
  },
  clearToken(state) {
    state.token = "";
  },
  setloginemail(state, value) {
    state.loginemail = value;
  },
  setcografyadialog(state, value) {
    state.cografyadialogshow = value;
  },
  setyasdialog(state, value) {
    state.yasdialogshow = value;
  },
  setUserLogData(state, data) {
    state.UserLogData = data;
  },
  setDownloadstatus(state, data) {
    state.downloadstatus = data;
  },
  setisamplingloading(state,val){
    state.isamplingloading = val;
  },
};
