<template>
  <v-row justify="center">
    <v-dialog v-model="cografyadialog" persistent max-width="600px">
      <v-card>
        <v-card-title>
          <span class="headline">Ek Ä°l SeÃ§imi</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-autocomplete
                  v-model="values"
                  :items="cografyaEkSecim"
                  label="IL"
                  :allow-overflow="false"
                  counter
                  multiple
                  dense
                  auto-select-first
                  chips
                  small-chips
                  deletable-chips
                  clearable
                  item-color="itemcolor"
                >
                </v-autocomplete>
              </v-col>
            </v-row>
          </v-container>
          <small>*Burada yalnÄ±z seÃ§ilen bÃ¶lgelere ait iller listelenir.</small><br/>
          <small>*SeÃ§ilen bÃ¶lgelerin temsilci illeri seÃ§ilmese de listeye eklenir.</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="red lighten-2"
            text
            @click="clearvalue"
          >
            Kapat
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="setvalue"
          >
            Kaydet
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import { Cografya } from "../mixins/dataCografya";
import { mapGetters,mapMutations } from "vuex";

export default {
  mixins: [Cografya],
  data: () => ({
    // values: [],
  }),
  computed: {
    ...mapGetters(["yasdialog","cografyadialog","getekcografyaselection","getcografyaselection","getcografya"]),
    values: {
      get() {
        return this.getekcografyaselection;
      },
      set(value) {
        this.Upd_ekcografyaselection(value);
      },
    },
    cografyaEkSecim() {
      const items = [];
      const cografya = this.getcografya;
      const getcografyaselection = this.getcografyaselection;
      this.formdata.cografya.il.items.forEach(function(data) {
        if (
          getcografyaselection.indexOf(
            cografya == 4 ? data.nuts12code : data.nuts26code
          ) > -1
        ) {
          if (!data.text.includes(data.nuts12)){
            data.text =  data.nuts12 + ' > ' + data.text 
          }
          items.push(data)
        }
      });
      return items;
    },
  },
  methods: {
    ...mapMutations(["setcografyadialog","Upd_ekcografyaselection"]),
    setvalue() {
      this.setcografyadialog(false);
      const payload = this.values;
      const cografya = this.getcografya;
      const py = this.formdata.cografya;
      this.getcografyaselection.forEach(function(data) {
        // Nuts Ã¶zel seÃ§imlerinde temsilci il seÃ§ilmese bile array iÃ§ine eklemek gerekiyor.
        const val = cografya == 4 ? py.nuts1.items : py.nuts2.items;
        const city = val.filter(function(x) {
          return x.value === data;
        });
        const v = city.map((y) => y.city)[0];
        if (payload.indexOf(v) == -1) payload.push(v);
      });
      // console.log([...new Set(payload)]);
      this.Upd_ekcografyaselection([...new Set(payload)]);
    },
    clearvalue() {
      this.setcografyadialog(false);
      this.Upd_ekcografyaselection([]);
    },
  },
};
</script>
