export const sharedFunc = {
  methods: {
    deleteChip(itemNeedToRemove, array) {
      for (let i = 0; i < array.length; i += 1) {
        if (array[parseInt(i, 10)] === itemNeedToRemove) {
          array.splice(i, 1);
        }
      }
    },
    M_Selectioner(val, mutations) {
      this.$store.commit(
        mutations,
        val.map((x) => x.value)
      );
    },
  },
};
