<template>
  <div>
    <v-row id="ses" class="pa-2">
      <v-select
        :items="formdata.ses.items"
        v-model="formdata.ses.selection"
        @change="Mseschange"
        :return-object="true"
        prepend-inner-icon="mdi-chart-bubble"
        label="Ses gruplarÄ±"
        dense
        outlined
      ></v-select>
    </v-row>
    <v-row id="ses" class="pa-2">
      <v-select
        :items="formdata.ses.defaultvalues"
        v-model="formdata.ses.defaultselection"
        item-color="itemcolor"
        :return-object="true"
        label="Ses"
        :auto-select-first="true"
        @change="
          M_Selectioner(formdata.ses.defaultselection, 'Upd_sesselection')
        "
        placeholder="Ses grubu seÃ§iniz."
        outlined
        clearable
        multiple
        dense
      >
        <template #selection="{ item }">
          <v-chip
            :color="item.color"
            text-color="white"
            small
          >
            {{ item.text }}
          </v-chip>
        </template>
      </v-select>
    </v-row>
  </div>
</template>

<script>
import { mapMutations } from "vuex";
import { Ses } from "../../mixins/dataSes";
import { sharedFunc } from "../../mixins/sharedFunc";

export default {
  mixins: [Ses, sharedFunc],
  methods: {
    ...mapMutations(["Upd_ses", "Upd_sesselection"]),
    Mseschange(e) {
      const sestip = e.value == 1 ? "sesgrup" : "sesacik";
      const val = this.formdata.ses[sestip];
      this.formdata.ses.defaultvalues = val.items;
      this.formdata.ses.defaultselection = val.selection;
      this.Upd_ses(this.formdata.ses.selection.value);
      this.Upd_sesselection(val.selection.map((val) => val.value));
    },
  },
};
</script>

<style scoped>
@media (max-width: 1500px) {
  #ses {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}
</style>
