<template>
  <div>
    <v-row id="yas" class="pa-2">
      <v-select
        :items="formdata.yas.items"
        v-model="formdata.yas.selection"
        @change="Myaschange"
        :return-object="true"
        prepend-inner-icon="mdi-chart-bubble"
        label="YaÅ GruplarÄ±"
        dense
        outlined
      ></v-select>
    </v-row>
    <v-row id="yas" class="pa-2">
      <v-col class="pa-0 my-0" cols="12" v-show="yascols == 11">
        <v-combobox
          :items="getozelyasselection"
          v-model="getozelyasselection"
          item-color="itemcolor"
          placeholder="Ãzel YaÅ grubu seÃ§mek iÃ§in tÄ±klayÄ±nÄ±z."
          :prepend-inner-icon="getozelyasselection.length == 0 ? 'mdi-alert' : ''"
          :readonly="true"
          @click="setyasdialog(true)"
          :error="
            formdata.yas.selection.value == 5 && getozelyasselection.length == 0
              ? true
              : false
          "
          outlined
          multiple
          counter
          dense
        >
          <template v-slot:selection="{ item, index }">
            <v-chip v-if="index < 3" color="chipcolor" text-color="white" small>
              <span>{{ item.text }}</span>
            </v-chip>
            <span v-if="index === 3" class="grey--text caption">
              (+{{ getozelyasselection.length - 3 }} diÄer)
            </span>
          </template>
        </v-combobox>
      </v-col>
      <v-col class="pa-0 my-0" v-show="yascols == 12">
        <v-select
          :items="formdata.yas.defaultvalues"
          v-model="formdata.yas.defaultselection"
          item-color="itemcolor"
          placeholder="YaÅ grubu seÃ§iniz."
          :prepend-inner-icon="formdata.yas.defaultselection.length == 0 ? 'mdi-alert' : ''"
          :return-object="true"
          :error="
            formdata.yas.selection.value != 5 &&
            formdata.yas.defaultselection.length == 0
              ? true
              : false
          "
          @change="
            M_Selectioner(formdata.yas.defaultselection, 'Upd_yasselection')
          "
          :auto-select-first="true"
          clearable
          required
          outlined
          multiple
          counter
          dense
        >
          <template v-slot:selection="{ item, index }">
            <v-chip v-if="index < 3" color="chipcolor" text-color="white" small>
              <span>{{ item.text }}</span>
            </v-chip>
            <span v-if="index === 3" class="grey--text caption">
              (+{{ formdata.yas.defaultselection.length - 3 }} diÄer)
            </span>
          </template>
        </v-select>
      </v-col>
    </v-row>
    <YasDialog />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import { Yas } from "../../mixins/dataYas";
import { sharedFunc } from "../../mixins/sharedFunc";
import YasDialog from "../YasDialog";

export default {
  mixins: [Yas, sharedFunc],
  data() {
    return {
      yascols: 12,
    };
  },
  components: {
      YasDialog,
  },
  computed: {
    ...mapGetters(["getozelyasselection"]),
  },
  methods: {
    ...mapMutations(["Upd_yas", "Upd_yasselection", "setyasdialog"]),
    Myaschange(e) {
      this.formdata.yas.defaultselection = new Array();
      this.formdata.yas.defaultvalues = new Array();
      this.yascols = 12;
      const yasmap = {
        1: "besliyas",
        2: "acikyas",
        3: "onbesplus1",
        4: "onbesplus2",
        6: "ustuyas",
      };
      if (e.value == 5) {
        this.yascols = 11;
      } else {
        const val = this.formdata.yas[yasmap[e.value]];
        this.formdata.yas.defaultselection = val.selection;
        this.formdata.yas.defaultvalues = val.items;
        this.Upd_yasselection(val.selection.map((x) => x.value));
      }
      this.Upd_yas(this.formdata.yas.selection.value);
    },
  },
};
</script>

<style scoped>
@media (max-width: 1500px) {
  #yas {
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}
</style>
